@import url('https://fonts.googleapis.com/css2?family=Questrial&display=swap');

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  color: inherit; /*for <button> and <a>*/
}

:root {
  --margin-y-md: 3rem;
  --box-shad-md: 1rem 2rem 2rem rgba(0, 0, 0, 0.19),
    0 6px 6px rgba(0, 0, 0, 0.23);
}

html {
  font-size: 62.5%;
}

@media only screen and (max-width: 1200px) {
  html {
    font-size: 56.25%;
  }
}
@media only screen and (max-width: 900px) {
  html {
    font-size: 50%;
  }
}
@media only screen and (max-width: 600px) {
  html {
    font-size: 43.75%;
  }
}

#root {
  height: 100vh;
  overflow: hidden;
}

.app {
  height: 100%;
  padding: 1.5rem 4rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  user-select: none;
  font-family: 'Questrial';
  color: white;
  text-align: center;
  transition: background-color 0.9s ease-in;
}

@media only screen and (max-width: 600px) {
  .app {
    padding: 0 3rem;
  }
}

.heading-primary {
  font-size: 5rem;
  letter-spacing: 5px;
  margin-bottom: var(--margin-y-md);
  animation: fadeIn 2s 1s backwards;
}

.heading-tertiary {
  font-size: 2.5rem;
  letter-spacing: 1.5px;
  margin-bottom: var(--margin-y-md);
  line-height: 3rem;
  animation: fadeIn 2s 2.3s backwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: initial;
  }
}

.quote-card {
  background: rgb(255, 235, 235);
  padding: 2rem;
  max-width: 114rem;
  margin: 0 auto;
  border-radius: 1rem;
  box-shadow: var(--box-shad-md);
  animation: fadeIn 1.2s ease-in;
}

@media only screen and (max-width: 600px) {
  .quote-card {
    padding: 1rem 1rem;
  }
}

.fa.fa-quote-left {
  margin-right: 1rem;
}

.quote {
  transition: opacity 1.1s ease-in-out, color 1s ease-in;
}

.fade {
  opacity: 0;
}

.quote-box {
  padding: 1.2rem 1.5rem;
  font-size: 3.6rem;
  font-weight: 600;
  letter-spacing: 1.5px;
  line-height: 5rem;
  max-height: 55vh;
  overflow: auto;
  user-select: text;
}

@media only screen and (max-width: 600px) {
  .quote-box {
    padding: 0.5rem;
    max-height: 40vh;
  }
}

@media only screen and (max-width: 600px) {
  .quote-box {
    font-size: 3rem;
    line-height: 4.2rem;
  }
}

.quote-box svg {
  vertical-align: -0.5rem;
}

.quote-author {
  font-size: 3.2rem;
  text-align: right;
  font-style: italic;
  margin: 1rem 0 4rem;
  user-select: text;
}

@media only screen and (max-width: 600px) {
  .quote-author {
    margin-bottom: 2rem;
  }
}

.buttons-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.buttons-container svg {
  vertical-align: -5px;
}

.buttons-container button {
  font-size: 3rem;
  box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.19), 0 1px 1px rgba(0, 0, 0, 0.23);
  margin: 3px;
  border-radius: 0.5rem;
  border: 0;
}

.buttons-container button {
  padding: 0;
  opacity: 0.85;
  transition: transform 0.2s, background-color 1s ease-in;
}

button.shuffle {
  padding: 0.9rem 1.1rem;
}

.buttons-container button:focus {
  outline: 0;
}

.buttons-container button:hover {
  box-shadow: 1.5px 3px 3px rgba(0, 0, 0, 0.19), 0 1px 1px rgba(0, 0, 0, 0.23);
  cursor: pointer;
  opacity: 1;
  transform: scale(0.95);
}
.buttons-container button:active {
  transform: initial;
}

a {
  display: block;
  padding: 0.9rem 1.1rem;
}

.footer {
  font-size: 2rem;
  margin-top: var(--margin-y-md);
}

@media only screen and (max-width: 600px) {
  .footer {
    font-size: 2.5rem;
  }
}

.loader {
  margin: 0 auto;
  display: flex;
  transition: opacity 0.3s ease-in;
}

.loading-text {
  margin-top: var(--margin-y-md);
  font-size: 4rem;
}

.circle {
  border-radius: 50%;
  height: 0.6rem;
  width: 0.6rem;
  margin: 0 0.5rem;
  background: white;
  animation: bounce 0.5s ease-in infinite;
}

.circle:nth-of-type(2) {
  animation-delay: 0.1s;
}

.circle:nth-of-type(3) {
  animation-delay: 0.2s;
}

@keyframes bounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-0.5rem);
  }
}

.click-cta {
  margin: 0 auto;
  font-size: 6rem;
  width: 15%;
  background: transparent;
  cursor: pointer;
  border: none;
  animation: fromBottom 1.2s 3.5s ease-out backwards;
  transition: initial;
  padding: 0.5rem 4rem;
  border-radius: 10rem;
  transition: all 0.3s;
}

.click-cta:hover {
  background: white;
  color: black;
}

.click-cta svg {
  vertical-align: -0.8rem;
}

@keyframes fromBottom {
  0% {
    opacity: 0;
    transform: translateY(3rem);
  }

  80% {
    transform: translateY(-0.3rem);
  }

  100% {
    transform: initial;
    opacity: initial;
  }
}

.enter-cta {
  font-size: 2.5rem;
  margin-top: var(--margin-y-md);
  animation: fromBottom 1.2s 4.7s ease-out backwards;
}

@media only screen and (max-width: 600px) {
  .enter-cta.mob {
    display: none;
  }
}
